import history from '../router/history';

export const changePage = pathname => {
  history.push(pathname);
};

export const goBack = () => {
  history.goBack();
};

export const searchfy = text => {
  return text
    .toString()
    .toLowerCase()
    .replace(/\s+/g, '+')
    .replace(new RegExp('[]', 'gi'), 'a')
    .replace(new RegExp('[]', 'gi'), 'e')
    .replace(new RegExp('[]', 'gi'), 'i')
    .replace(new RegExp('[]', 'gi'), 'o')
    .replace(new RegExp('[]', 'gi'), 'u')
    .replace(new RegExp('[]', 'gi'), 'c');
};
